
.container{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    text-align: left;
    position: relative;
    font-size: 20px;
    background-color: rgb(104, 15, 104);
    color: white;
}
.info{
    display: flex;
    padding: 10px;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction:row;
    
}
.data{
    justify-content: space-between;
    padding: 10px;
}
h1,h2,p{
    font-family: 'Vujahday Script', cursive;
margin: 10px;
}
.logo{
    display: flex;
    margin:0;
    padding: 3px;
    height: 60px;
    width: 60px;
    border-radius: 0 50% 50% 0;
    background-color: white;
}
.nav{
    display: flex;
    justify-content:space-between;
    flex-direction: row;
    text-align: center;
    font-family: 'Inconsolata', monospace;
    background-color: rgb(34, 26, 26);
    list-style: none;
}
.sideone{
    height: 350px;
    width: 350;
}
ul,li{
    display: flex;
    margin: 5px;
    padding: 5px;
    justify-content: space-between;
    text-align: center;
    flex-direction: row; 
    list-style: none;
    transition: 0.9s;
}
li:hover{
    box-shadow: 100px 0px 10px 5px yellow inset;

}
a{
    text-decoration: none;
    color: white;
}
a:hover{
    color: black;
}
.informativeimages{
    justify-content: center;
    text-align: right;
}

p{
    text-align: justify;
    max-width: 650px;
    color: gray;
    font-family: 'Indie Flower', cursive;
}
.lug{
    padding: 50px;
    display: flex;
    text-align: center;
    justify-content:space-around;
    flex-wrap: wrap;
    flex-direction: row;
    background-color: rgb(35, 156, 31);
}
.llogo{
    font-size: 100px;
    transition: 0.5s;
}
.llogo:hover{
    font-size: 150px;
}
.ltext{
    font-size: 15px;
}